.home{
    max-width: 1500px;
    margin: 0 auto;
}

.homeSliderContainer{
    width: 100%;
    height: 40vh;
    margin: 0 auto;
    position: relative;
    border-radius: 0.5rem;
}

.homeSlide{
    position: absolute;
    width: 100%;
    display: grid;
    place-items: center;
    transition: 0.2s ease-in-out all;
    text-align: center
}

.homeImg{
    width: 100%;
    height: 600px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,0.1),rgba(0,0,0,0));
    -webkit-mask-image:inear-gradient(to bottom, rgba(0,0,0,0.1),rgba(0,0,0,0)) ;
    z-index: -1;
    margin-bottom: -300px;
    transition: 0 all ease-in-out;
}

.homeRow{
    display: flex;
    z-index: 1;
    margin:5px;
}