.Login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.loginLogo{
    width: 100px;
    object-fit: contain;
    margin: 25px 0;
}

.LoginContainer{
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 25px;
    border: 1px solid gray ;
}

.LoginContainer h1 {
    margin: 25px;
    font-size: 40px;
    text-align: center;
    font-weight: 400;
}

.LoginContainer form input {
    width: 99%;
    height: 33px;
    border-radius: 10px;
    border: 1px solid gray;
    margin: 9px 0;
}

.LoginContainer button{
    height: 33px;
    width: 100%;
    background: #f0c14b;
    border-color: #a88734 #9c7e31 #846a29;
    cursor: pointer;
    color:#333;
    border-radius: 10px;
    margin-top: 14px;
}

.LoginContainer p {
    font-size: 15px;
    margin: 9px 0;
}