.Products{
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    max-height: 400px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    z-index: 1;
    background: #fff;    

}

.ProductInformation{
    height: 100px;
}

.ProductInformation p{
    text-align: center;
}

.ProductsGroup{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.Products img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.ProductRating{
    color: #f0c14b;
    display: flex;
}

.Products button{
    background: #f0c14b;
    border: 1px solid;
    width: 100%;
    padding: 5px 10px ;
    margin-top: 5px;
}
